@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');
 
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body, html{
  width: 100%;
  height: 100%;
}
ul,ol{
  list-style: none;
}
button, input{
  outline: 0 none;
  border: none;
  cursor: pointer;
}
a{
  text-decoration: none;
}
img{
  object-fit: cover;
}
* {
  font-family: 'Inter';
}
.container{
  max-width: 960px;
  width: 90%;
  margin: 0 auto;
}
.line{
  width: 100%;
  border-top: 1px solid #EAEAEA;
  display: block;
}

section{
  padding: 0 60px;
}
/* section.photoSlider{
  #slider{
    display: flex;
  }
} */
section.products{
  h3{
    font-weight: bold;
    font-size: 32px;
    line-height: 39px;
    padding: 40px 0;
  }
  .allCards{
    display: flex;
    flex-wrap: wrap;
  }
}
.topSection{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.search{
  position: relative;
  svg{
    position: absolute;
    left: 18px;
    top: 15px;
  }
  .searchClose{
    position: absolute;
    right: 10px;
    width: 18px;
    height: 18px;
    top: 13.5px;
    cursor: pointer;
  }
  input{
    color: #858585;
    padding: 0 46px;
    border: 1px solid #F3F3F3;
    box-sizing: border-box;
    border-radius: 10px;
    width: 250px;
    height: 45px;
    &:focus{
      border: 1px solid #dddcdc;
    }
    &:placeholder-shown{
      padding-left: 46px;
    }
    &::placeholder{
      font-size: 14px;
      line-height: 17px;
      color: #C4C4C4;

    }
  }
}
.favorites, .orders{
  padding-top: 50px;
  h3{
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    padding: 24px 0 11px;
  }
  p{
    font-size: 16px;
    color: rgba(0,0,0,0.4);
    padding-bottom: 20px;
  }
  .favorites p{
    line-height: 19px;
  }
  .orders p{
    line-height: 24px;
  }
.emptyFavorites, .emptyOrders{
  display: flex;
  align-items: center;
  flex-direction: column;
}
  button.btn{
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    width: 325px;
    height: 55px;
    background: #9DD558;
    border-radius: 18px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid transparent;
    svg{
      transform: rotate(180deg);
      margin-right: 24px;
    }
  }
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  .flex{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
}